






















import { reactive } from '@vue/composition-api';
interface OptionsItem {
	tag: string;
	tagStyle: {
		bgColor: string,
		color: string
	};
	title: string;
	subtitle: string;
	img: string;
	bgColor: string
} 
const props = withDefaults(
  defineProps<{
    options: OptionsItem[];
  }>(),
  {
    options: () => [],
  }
);

const state = reactive({
  dataOptions: props.options as OptionsItem[],
});
